/*
    Shop နဲ့ Category Dropdown နှစ်ခု လိုမယ်။
    အဲ့တော အပေါ်မူတည်ပြီး menu တွေ ေပြာင်းနေမယ်ပေါ့ ဒါမျိုးလား?

    table နဲ့ ခွဲပြီး ပြထားလဲ ရတယ်။
    Category တစ်ခုကို table တစ်ခုပေါ့။
    ဒါမှ မဟုတ် အကုန် တန်းစီပြထားလဲ ရတယ်။ 
    အကုန် တန်းစီပြလိုက်မယ်။
    တစ်ဆိုင်ကို ဇယားတစ်ခု။

*/

import {
  Box,
  Button,
  Chip,
  Container,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Stack,
  Table,
  Typography,
} from '@mui/material';

import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { useDispatch, useSelector } from 'react-redux';
import {
  selectItemDeleteStatus,
  selectItemError,
  selectMenuDeleteStatus,
  selectMenuError,
  selectSelectedItem,
  selectSelectedMenu,
  selectShopList,
  setItemDeleteStatus,
  setMenuDeleteStatus,
  setSelectedItem,
  setSelectedMenu,
  setSelectedShop,
} from 'features/main/mainSlice';
import { Delete, Edit } from '@mui/icons-material';
import Iconify from 'template/components/iconify';
import { useRouter } from 'template/routes/hooks';
import { useState } from 'react';
import { deleteMenuAsyncThunk } from 'features/menu/menuSlice';
import { selectAccessToken } from 'features/user/userSlice';
import { deleteItemAsyncThunk } from 'features/item/itemSlice';
import NoShopComponent from 'template/sections/shops/noshop/no-shop-component';

// const shopList = [1, 2, 3];
export default function ItemList() {
  const shopList = useSelector(selectShopList);
  // const selectedMenu = useSelector(selectSelectedMenu);
  const selectedItem = useSelector(selectSelectedItem);
  const accessToken = useSelector(selectAccessToken);
  const itemDeleteStatus = useSelector(selectItemDeleteStatus);
  const itemError = useSelector(selectItemError);

  const dispatch = useDispatch();
  const router = useRouter();

  // ---------- internal state ------------ //
  // for delete confirmation dialog
  const [deleteConfirmationDialog, setDeleteConfirmationDialog] = useState(false);

  const newItemClicked = (shop) => {
    dispatch(setSelectedShop(shop.id));
    router.push('/items/create');
  };

  const editItemClicked = (shop, item, menu, category) => {
    // item should have a full structure
    // like menu,
    // menu also should have full structure like category
    // menu.category = category;
    // item.menu = menu;
    let newObj = { ...item, menu };
    dispatch(setSelectedItem(newObj));
    dispatch(setSelectedShop(shop.id));
    router.push(`/items/${item.id}/detail`);
  };

  const deleteItemClicked = (item) => {
    dispatch(setSelectedItem(item));
    setDeleteConfirmationDialog(true);
  };

  const cancelDelete = () => {
    // close dialog
    setDeleteConfirmationDialog(false);
  };
  const confirmDelete = (id, access_token) => {
    // close dialog
    setDeleteConfirmationDialog(false);
    let formData = new FormData();
    formData.append('access_token', access_token);
    formData.append('id', id);
    dispatch(deleteItemAsyncThunk(formData));
  };

  const handleErrorDialogClose = () => {
    // set error status to idle
    dispatch(setItemDeleteStatus('idle'));
  };

  return (
    <>
      <Container component="main" maxWidth="lg">
        {shopList.length === 0 ? <NoShopComponent /> : <Container></Container>}
        {shopList.map((shop) => (
          <Box key={shop.id}>
            {/* categories header */}
            <Stack direction="row" alignItems="center" justifyContent="space-between" mb={5} mt={5}>
              <Typography variant="h4">{shop.name}'s Items</Typography>

              <Button
                onClick={() => newItemClicked(shop)}
                // component={Link}
                // to={`/categories/create?shop_id=${shop.id}`}
                variant="contained"
                color="inherit"
                startIcon={<Iconify icon="eva:plus-fill" />}
              >
                New Item
              </Button>
            </Stack>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell align="left">အမည်</TableCell>
                  <TableCell align="right">Menu</TableCell>
                  <TableCell align="right">Category</TableCell>
                  <TableCell align="right">ခလုပ်များ</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {shop.categories.map((category) =>
                  category.menus.map((menu) =>
                    menu.items.map((item) => (
                      <TableRow
                        key={item.id}
                        sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                      >
                        <TableCell component="th" scope="row">
                          {item.name}
                        </TableCell>
                        <TableCell align="right">
                          <Chip label={menu.name} variant="outlined" />
                        </TableCell>
                        <TableCell align="right">
                          <Chip label={category.name} variant="outlined" />
                        </TableCell>
                        <TableCell align="right">
                          <Button onClick={() => editItemClicked(shop, item, menu, category)}>
                            <Edit />
                          </Button>
                          <Button onClick={() => deleteItemClicked(item)}>
                            <Delete />
                          </Button>
                        </TableCell>
                      </TableRow>
                    ))
                  )
                )}
              </TableBody>
            </Table>
          </Box>
        ))}
      </Container>

      {/* delete confirmation dialog */}
      <Dialog
        open={deleteConfirmationDialog}
        // onClose={() => handleClose(id)}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">Delete Confirmation!</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Are you sure to delete {selectedItem.name} ?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => cancelDelete(0)} autoFocus>
            Cancel
          </Button>
          <Button onClick={() => confirmDelete(selectedItem.id, accessToken)} autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>

      {/* delete error dialog */}
      <Dialog
        open={itemDeleteStatus === 'error'}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">Error!</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">{itemError}</DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleErrorDialogClose} autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
}
